import store from '../store'
import { fromJS } from 'immutable'

let timer

//显示loading
const showLoading = (delay = 0) => {
  timer = setTimeout(() => {
    store.dispatch({
      type: 'SET_LIGHT_STATE',
      key: ['isLoading'],
      value: true,
    })
  }, delay)
}

//隐藏loading
const hideLoading = () => {
  clearTimeout(timer)
  store.dispatch({
    type: 'SET_LIGHT_STATE',
    key: ['isLoading'],
    value: false,
  })
}

//把url里的查询字段转换成对象
const parseQueryString = (url) => {
  let params = {}
  let arr = url.split('?')
  if (arr.length <= 1) {
    return params
  }
  arr = arr[1].split('&')
  for (let i = 0, l = arr.length; i < l; i++) {
    let a = arr[i].split('=')
    params[a[0]] = a[1]
  }
  return params
}

//包路由里的查询字段转换成对象
const getRouterSearchObj = (props) => {
  const {
    location: { search },
  } = props
  const routerSearchObj = parseQueryString(search)
  return routerSearchObj
}

//获取主机名
const getHost = (url) => {
  var reg = /http(s)?:\/\/([A-Za-z0123456789:.]+)\/\S+/
  var result = reg.exec(url)
  if (result) {
    return 'http://' + result[2]
  }
}

const deepClone = (obj) => {
  return fromJS(obj).toJS()
}

const imgUrlFormat = (url) => {
  if (process.env.REACT_APP_MODE === 'dev' || process.env.REACT_APP_MODE === 'test') {
    let tempUrl = [...url]
    tempUrl.splice(7, 0, 'test_')
    tempUrl = tempUrl.join('')
    return tempUrl
  } else {
    return url
  }
}

export {
  showLoading,
  hideLoading,
  parseQueryString,
  getRouterSearchObj,
  getHost,
  deepClone,
  imgUrlFormat,
}
