import React from 'react'
import { Button, Upload, message } from 'antd'
import { getHost } from '../../utils/tools'
import urls from '../../api/urls'

export default function UploadFile({ value = {}, onChange }) {

  let defaultValue = {
    uid: '-1',
    name: value.name,
    status: 'done',
    url: value.url,
  }

  const fileList = [];
  if (value.name && value.url) {
    fileList.push(defaultValue)
  }
  const uploadProps = {
    name: 'img',
    action: urls.light.uploadLogo,
    maxCount: 1,
    listType: "picture",
    defaultFileList:[...fileList],
    accept: '.jpg,.pdf',
    onChange(info) {
      if (info.file.status !== 'uploading') {
        console.log(info.file, info.fileList)
        onChange(undefined)
      }
      if (info.file.status === 'done') {
        message.success(`${info.file.name} 上传成功`)
        if (info.file.response.code === 200) {
          console.log(info.file.xhr.responseURL)
          const imgUrl = `${getHost(info.file.xhr.responseURL)}/${info.file.response.data.filename}`
          console.log(imgUrl)
          onChange({
            name: info.file.response.data.filename,
            url: imgUrl,
          })          
        }
      } else if (info.file.status === 'error') {
        message.error(`${info.file.name} 上传失败`)
      }
    },
  }

  return (
    <span>
      <Upload {...uploadProps}>
        <Button>上传授权书</Button>
        <span className="m-upload-text">文件格式jpg|pdf, 大小4M以内</span>
      </Upload>
    </span>
  )
}
