import React, { useState } from 'react'
import { Form, Row, Col, Button } from 'antd'
import Icon from './Icon'

function Search(props) {
  const [isExpand, setExpand] = useState(false)
  //operateSpan控制搜索、导出、重置、展开组件加在一起的宽度占总宽度的比例 operateSpan / 24
  //gutter={24}
  const { operateSpan = 11 } = props
  const {
    initialValues = {},
    isExport = false,
    isReset = false,
    isFoldable = false,
  } = props
  const [form] = Form.useForm()

  const operate = (
    <>
      <Col
        span={isExpand ? 24 : operateSpan}
        className={`m-search-operate ${isExpand ? 'active' : ''}`}
      >
        <Button type="primary" className="m-space" htmlType="submit">
          <Icon name="search" className="m-tool-btn-icon"></Icon>
          搜索
        </Button>
        {isExport && (
          <Button type="primary" className="m-space">
            <Icon name="export" className="m-tool-btn-icon"></Icon>
            导出
          </Button>
        )}
        {isReset && (
          <Button
            className="m-space"
            onClick={() => {
              form.resetFields()
              props.onSearch({ searchParams: initialValues })
            }}
          >
            <Icon name="reset" className="m-tool-btn-icon"></Icon>
            重置
          </Button>
        )}
        {isFoldable && (
          <Button
            className="m-space"
            type="link"
            onClick={() => {
              setExpand(!isExpand)
            }}
          >
            {
              <Icon
                name="arrow"
                className={`m-tool-btn-icon ${
                  isExpand ? 'rotate90' : 'rotate_90'
                }`}
              ></Icon>
            }
            {isExpand ? '收起' : '展开'}
          </Button>
        )}
      </Col>
    </>
  )

  return (
    <div className="m-content-search">
      <Form
        form={form}
        name="advanced_search"
        className="ant-advanced-search-form"
        onFinish={(values) =>
          props.onSearch({ searchParams: values, type: 'searchForm' })
        }
        initialValues={initialValues}
      >
        <Row gutter={24}>
          {props.getSearchFields(isExpand)}
          {!isExpand && operate}
        </Row>
        {isExpand && <Row>{operate}</Row>}
      </Form>
    </div>
  )
}

export default Search
