import { useState, useEffect } from 'react'
import Api from '../../../../../../api'
import { Modal, Form } from 'antd'

const { confirm } = Modal

let historySearchParams = {}
export default function useList(props) {
  const [total, setTotal] = useState()
  const [current, setCurrent] = useState(1)
  //把dataSource和pageSize单独放在一起是为了避免切换pageSize时的bug
  const [state, setState] = useState({
    dataSource: [],
    pageSize: 10
  })
  const [form] = Form.useForm()
  //控制对话框显示和隐藏
  const [isModalVisible, setIsModalVisible] = useState(false)
  //添加表单默认值
  const [initValues, setInitValues] = useState({})
  const [type, setType] = useState('add')
  const [modalTitle, setModalTitle] = useState()

  const addInitValues = {}

  //搜索
  const handleSearch = ({
    page = 1,
    pageSize = state.pageSize,
    searchParams = {},
    type,
  } = {}) => {
    if (type === 'searchForm') {
      historySearchParams = searchParams
    } if (type === 'pagination') {
      searchParams = historySearchParams
    }
    
    Api.light
      .templateSearch({ page, pageSize, searchParams })
      .then((res) => {
        if (res.code === 200) {
          setState({
            dataSource: res.data.list,
            pageSize: res.data.pageSize
          })
          setTotal(res.data.total)
          setCurrent(res.data.current)
        }
      })
  }

  //删除
  const handleDelete = (record) => {
    console.log('删除, id:', record.id)
    confirm({
      title: '确认要删除吗？',
      onOk() {
        Api.light.templateDelete({ ids: [record.id] }).then((res) => {
          if (res.code === 200) {
            handleSearch()
          }
        })
      },
    })
  }

  //显示添加对话框
  const handleAdd = () => {
    setType('add')
    setInitValues(addInitValues)
    console.log(addInitValues)
    setModalTitle('添加')
    setIsModalVisible(true)
  }

  //显示编辑对话框
  const handleEdit = (record) => {
    console.log('编辑, id:', record)
    setType('edit')
    setInitValues({ ...initValues, ...record })
    setModalTitle('编辑')
    setIsModalVisible(true)
  }

  //添加或编辑
  const handleFinish = (values) => {
    console.log('Success:', values)
    if (type === 'add') {
      Api.light.templateAdd({ dataItem: values }).then((res) => {
        if (res.code === 200) {
          setIsModalVisible(false)
          handleSearch()
        }
      })
    } else if (type === 'edit') {
      Api.light
        .templateEdit({
          id: initValues.id,
          dataItem: { ...initValues, ...values },
        })
        .then((res) => {
          if (res.code === 200) {
            setIsModalVisible(false)
            handleSearch()
          }
        })
    }
  }

  //校验失败
  const handleFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo)
  }

  //查看
  const handleCheck = (record) => {
    setType('check')
    setInitValues({ ...initValues, ...record })
    setModalTitle('查看')
    setIsModalVisible(true)
  }

  // 挂载完请求第一页数据
  useEffect(() => {
    handleSearch()
    // eslint-disable-next-line 
  }, [])

  // 每次打开对话框时要重置一下表单，防止表单记忆上一次的值，添加、编辑、查看共用一个表单
  useEffect(() => {
    form.resetFields()
  }, [isModalVisible, form])


  return {
    dataSource: state.dataSource,
    total,
    current,
    pageSize: state.pageSize,
    isModalVisible,
    initValues,
    type,
    form,
    modalTitle,
    handleSearch,
    handleDelete,
    handleAdd,
    handleEdit,
    handleCheck,
    setIsModalVisible,
    handleFinish,
    handleFinishFailed,
  }
}
