import React, { useState } from 'react'
import { Select } from 'antd'
const { Option } = Select

export default function SelectHasFooter({
  value,
  onChange,
  footer,
  relatedBrandOption,
  placeholder,
  getPopupContainer,
}) {
  const [myValue, setMyValue] = useState()

  const handleChange = (value) => {
    setMyValue(value)
    onChange(value)
  }

  return (
    <>
      <Select
        value={value || myValue}
        onChange={(value) => handleChange(value)}
        placeholder={placeholder}
        allowClear
        getPopupContainer={getPopupContainer}
      >
        {relatedBrandOption.map((item) => (
          <Option key={item.value} value={item.value}>
            {item.title}
          </Option>
        ))}
      </Select>
      {footer}
    </>
  )
}
